import { useState } from 'react';
import ImgComponent from '../common/ImgComponent';
import TmboxEvent from '../card/TmboxEvent';


interface Props{
    IndClass?: string;
    IndTitle?: string;
}

const EventStIndicator = ({ IndClass, IndTitle }:Props) => {

    // 
    const [toggleInd, setToggleInd] = useState(false);
    const clickInd = () => {
        setToggleInd(!toggleInd);
    }

    return (
        <article className={IndClass}>
            <div className="indi_top" onClick={clickInd}>
                <h1>{IndTitle}</h1>
                <p className={toggleInd? 'arr_up m-r-8 down':'arr_up m-r-8'}>
                    <ImgComponent src={'arrow_up16.svg'} />
                </p>
            </div>
            <div className={toggleInd? 'indi_conts del':'indi_conts'}>
                <div className='indi_vert416 scroll sc_y'>
                    <TmboxEvent boxClass={'tmbox'} datetime={'2022 01 23 21:12'} labelColor={'grade_color03'} labelWord={''}
                    tmTitle={'<>  JC JC   /  JC JC'} />
                    <TmboxEvent boxClass={'tmbox'} datetime={'2022 01 23 21:12'} labelColor={'dust_color03'} labelWord={''}
                    tmTitle={'<>  JC JC   /  JC JC'} />
                    <TmboxEvent boxClass={'tmbox'} datetime={'2022 01 23 21:12'} labelColor={'label_color02'} labelWord={''}
                    tmTitle={'<>  JC JC   /'} />
                    <TmboxEvent boxClass={'tmbox'} datetime={'2022 01 23 21:12'} labelColor={'label_color07_1'} labelWord={''}
                    tmTitle={'<>  JC JC   /'} />
                    <TmboxEvent boxClass={'tmbox'} datetime={'2022 01 23 21:12'} labelColor={'dust_color03_3'} labelWord={''}
                    tmTitle={'<>  JC JC   /'} />
                    <TmboxEvent boxClass={'tmbox'} datetime={'2022 01 23 21:12'} labelColor={'grade_color03'} labelWord={''}
                    tmTitle={'<>  JC JC   /'} />
                </div>
            </div>
        </article>
    );
};

export default EventStIndicator;