import { useState, CSSProperties } from 'react';
import ImgComponent from '../common/ImgComponent';
import MarkerComponent from '../map/MarkerComponent';
import PopupComponent from './PopupComponent';


interface Props{
    style?: CSSProperties; 
    title?: string;
}

const MarkerPopVds = ({ style, title }:Props) => {

    //  
    const [markStatus, setMarkStatus] = useState<boolean>(true);
    const toggleMark = () => {
      setMarkStatus(!markStatus);
    }
    const closeMark = () =>{
      setMarkStatus(false);
    }
    
    return (
        <article className={'marker_container'} style={style}>
            <div className='tooltip_parents'>
                <MarkerComponent onClick={toggleMark} className={markStatus? 'marker mk48 on' : 'marker mk48'}>
                    <ImgComponent className='img_off' src={'radar_normal_48.svg'} />
                    <ImgComponent className='img_selected' src={'radar_normal_selected_48.svg'} />
                </MarkerComponent>

                <PopupComponent boxClass={markStatus? 'popover_box w480 map_top dpblock' : 'popover_box w480 map_top'} titleBgClass={'popup_title_bg'} 
                titleClass={'popup_title'} title={title} close={closeMark}>
                    <div className="popup_contents p-20">
                        <table className="table_box m-b-12">
                            <caption className="hidden_tag">VDS </caption>
                            <thead>
                                <tr>
                                    <th className="tac"><p className='text_ellipsis w180'></p></th>
                                    <th className="tac"><p className='text_ellipsis w180'> ID</p></th>
                                    <th className="tac"><p className='text_ellipsis w80'></p></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr className="row_type2">
                                    <td className="tac">()</td>
                                    <td className="tac">CAM-0054</td>
                                    <td className="tac">
                                        <span className='fc_success'></span>
                                    </td>
                                </tr>                 
                            </tbody>
                        </table>
                        <table className='table_box grey'>
                            <tbody>
                                <tr className="row_type2">
                                    <td className=''>
                                        <p className='text_ellipsis tac'>(5) </p>
                                    </td>
                                    <td>
                                        <p className='text_ellipsis tac w300'>546 </p>
                                    </td>
                                </tr>
                                <tr className="row_type2">
                                    <td className=''>
                                        <p className='text_ellipsis tac'>(5)  </p>
                                    </td>
                                    <td>
                                        <p className='text_ellipsis tac w300'>67km/h</p>
                                    </td>
                                </tr>
                                <tr className="row_type2">
                                    <td className='tac'>
                                        <p className='text_ellipsis tac'></p>
                                    </td>
                                    <td>
                                        <p className='text_ellipsis tac w300'> </p>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </PopupComponent>
            </div>
        </article>
    )
}

export default MarkerPopVds;

