import TrRoute from './TrRoute';


interface Props{
    empty?: boolean;
}

const TableRouteList = ({ empty }:Props) => {
// 

    return (
        <table className="table_box table_route">
            <caption className="hidden_tag"> </caption>
            <thead>
                <tr>
                    <th className="tac"><p className='w40'></p></th>
                    <th className="tac"><p className='w66'></p></th>
                    <th className="tac"><p className='w66'></p></th>
                    <th className="tac"><p className='w66'></p></th>
                    <th className="tac"><p className='w28'></p></th>
                </tr>
            </thead>
            {
                empty === true? 
                <tbody>
                    <tr className={'row_type2'}>
                        <td colSpan={5} className="tac">
                            <p className='op04'>Empty text</p>
                        </td>
                    </tr>
                </tbody> :
                <tbody>
                    <TrRoute number={'013-4'} start={' ()'} arrive={' '} company={''} />
                    <TrRoute number={'013-1'} start={' ()'} arrive={' '} company={''} />
                    <TrRoute number={'013-2'} start={' ()'} arrive={' '} company={''} />
                    <TrRoute number={'013-3'} start={' ()'} arrive={' '} company={''} />
                    <TrRoute number={'013-4'} start={' ()'} arrive={' '} company={''} />
                    <TrRoute number={'013-4'} start={' ()'} arrive={' '} company={''} />
                    <TrRoute number={'013-4'} start={' ()'} arrive={' '} company={''} />
                    <TrRoute number={'013-4'} start={' ()'} arrive={' '} company={''} />
                </tbody>
            }
        </table>
    );
};

export default TableRouteList;