import { useState, CSSProperties } from 'react';
import ImgComponent from '../common/ImgComponent';
import MarkerComponent from '../map/MarkerComponent';
import PopupComponent from './PopupComponent';


interface Props{
    style?: CSSProperties; 
    title?: string;
}

const MarkerPopVms = ({ style, title }:Props) => {

    //  
    const [markStatus, setMarkStatus] = useState<boolean>(true);
    const toggleMark = () => {
      setMarkStatus(!markStatus);
    }
    const closeMark = () =>{
      setMarkStatus(false);
    }
    
    return (
        <article className={'marker_container'} style={style}>
            <div className='tooltip_parents'>
                <MarkerComponent onClick={toggleMark} className={markStatus? 'marker mk48 on' : 'marker mk48'}>
                    <ImgComponent className='img_off' src={'vms_normal_48.svg'} />
                    <ImgComponent className='img_selected' src={'vms_normal_selected_48.svg'} />
                </MarkerComponent>

                <PopupComponent boxClass={markStatus? 'popover_box w480 map_top dpblock' : 'popover_box w480 map_top'} titleBgClass={'popup_title_bg'} 
                titleClass={'popup_title'} title={title} close={closeMark}>
                    <div className="popup_contents p-20">
                        <div className="vms_area auto h96 m-b-12">
                            <p><span>( ) </span></p>
                            <p><span className='fc_green_light'>10.~19. ~ 10. 24. 09:00 ~ 18:00</span></p>
                            <p><span>   </span></p>
                        </div>
                        <div className="vms_area auto h96 m-b-12">
                            <p><span className='fc_error_code'>10. 21. ~ 10. 22.</span><span> ADEX 2023</span></p>
                            <p><span> </span> <span className='fc_error_code'> </span></p>
                            <p><span className='fc_green_light'>--</span></p>
                        </div>
                        <div className="vms_area auto h96 m-b-12">
                            <p><span>10. 6. 20:00 ~ 08:00 3  8</span></p>
                            <p><span className='fc_green_light'>   </span></p>
                        </div>
                        <table className="table_box">
                            <caption className="hidden_tag">VMS </caption>
                            <thead>
                                <tr>
                                    <th className="tac"><p className='text_ellipsis w140'></p></th>
                                    <th className="tac"><p className='text_ellipsis w140'> ID</p></th>
                                    <th className="tac"><p className='text_ellipsis w80'></p></th>
                                    <th className="tac"><p className='text_ellipsis w80'></p></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr className="row_type2">
                                    <td className="tac">()</td>
                                    <td className="tac">CAM-0054</td>
                                    <td className="tac">212</td>
                                    <td className="tac">
                                        <span className='fc_success'></span>
                                    </td>
                                </tr>                 
                            </tbody>
                        </table>
                    </div>
                </PopupComponent>
            </div>
        </article>
    )
}

export default MarkerPopVms;

